import { combineReducers } from 'redux';
import _ from 'lodash';

const TEST_STATE = {
  grid: [['Q', 'V', 'P', 'G', 'C', 'U', 'Y', 'I', 'Y', 'C', 'Q', 'E'],
         ['K', 'P', 'P', 'E', 'G', 'S', 'D', 'F', 'H', 'C', 'X', 'R'],
         ['Y', 'F', 'R', 'D', 'Z', 'S', 'Q', 'X', 'F', 'L', 'O', 'Z'],
         ['F', 'G', 'Q', 'O', 'W', 'Q', 'D', 'Q', 'U', 'J', 'I', 'K'],
         ['P', 'R', 'X', 'K', 'M', 'M', 'D', 'G', 'U', 'N', 'Q', 'V'],
         ['Y', 'A', 'I', 'P', 'B', 'E', 'D', 'T', 'C', 'Y', 'W', 'G'],
         ['E', 'F', 'B', 'S', 'A', 'R', 'T', 'W', 'Y', 'P', 'C', 'N'],
         ['T', 'A', 'S', 'G', 'S', 'A', 'G', 'H', 'C', 'G', 'C', 'K'],
         ['R', 'N', 'A', 'O', 'H', 'C', 'N', 'T', 'E', 'G', 'V', 'S'],
         ['W', 'A', 'O', 'E', 'E', 'N', 'C', 'A', 'Y', 'U', 'Z', 'O'],
         ['N', 'I', 'S', 'T', 'I', 'O', 'B', 'T', 'X', 'F', 'S', 'V'],
         ['Z', 'I', 'S', 'F', 'B', 'F', 'M', 'T', 'F', 'V', 'J', 'C'],
         ['P', 'R', 'X', 'K', 'M', 'M', 'D', 'G', 'U', 'N', 'Q', 'V'],
         ['F', 'G', 'Q', 'O', 'W', 'Q', 'D', 'Q', 'U', 'J', 'I', 'K'],
         ['Y', 'F', 'R', 'D', 'Z', 'S', 'Q', 'X', 'F', 'L', 'Q', 'Z'],
         ['F', 'I', 'S', 'U', 'B', 'N', 'M', 'T', 'K', 'V', 'J', 'C'],
         ['Q', 'V', 'P', 'G', 'C', 'U', 'Y', 'I', 'Y', 'C', 'Q', 'E'],
         ['Z', 'I', 'S', 'F', 'B', 'F', 'M', 'T', 'F', 'V', 'J', 'C'],
         ['Q', 'V', 'P', 'G', 'C', 'U', 'Y', 'I', 'Y', 'C', 'Q', 'E'],
         ['Z', 'Y', 'S', 'W', 'B', 'F', 'M', 'T', 'F', 'V', 'J', 'R']],
  settings: {
    expandMenu: false,
    showWordList: true,
    showWordHints: false
  },
  words: {
    TERRAFORM: {
      word: 'TERRAFORM',
      start: {x: 17, y: 0},
      end: {x: 17, y: 8}
    },
    PROMETHEUS: {
      word: 'PROMETHEUS',
      start: {x: 1, y: 1},
      end: {x: 10, y: 10}
    },
    GRAFANA: {
      word: 'GRAFANA',
      start: {x: 1, y: 3},
      end: {x: 1, y: 9}
    },
    ISTIO: {
      word: 'ISTIO',
      start: {x: 1, y: 10},
      end: {x: 5, y: 10}
    },
    BASH: {
      word: 'BASH',
      start: {x: 4, y: 5},
      end: {x: 4, y: 8}
    },
    KUBERNETES: {
      word: 'KUBERNETES',
      start: {x: 14, y: 2},
      end: {x: 14, y: 10}
    }
  }
};

export function grid(state = TEST_STATE.grid, action) {
  return state;
}

export function settings(state = TEST_STATE.settings, action) {
  switch (action.type) {
    case 'MERGE_SETTINGS':
      return {...state, ...action.updatedSettings };
    default:
      return state;
  }
}

export function words(state = TEST_STATE.words, action) {
  switch (action.type) {
    case 'SET_WORD_HINTED':
      if (state[action.word]) {
        return { ...state,
          [action.word]: { ...state[action.word], hinted: action.hinted }
        };
      } else {
        return state;
      }
    case 'ATTEMPT_SOLUTION':
      const word = Object.values(state).find(word =>
        (_.isEqual(word.start, action.start) && _.isEqual(word.end, action.end)) ||
        (_.isEqual(word.start, action.end) && _.isEqual(word.end, action.start)));
      if (word) {
        return { ...state,
          [word.word]: { ...word, solved: true }
        };
      } else {
        return state;
      }
    default:
      return state;
  }
}

export default combineReducers({
  grid,
  settings,
  words
});



